<template>
  <div id="menu">
    <div class="container">
      <h2>ROYALTY MENUS</h2>
      <p class="subtitle">
        Praesent blandit mollis dui a imperdiet. Vestibulum eu vehicula ante.
        Curabitur lacinia ipsum et porta ultrices. Morbi eu nunc convallis,
        ullamcorper ipsum nec
      </p>
      <div>
        <tabs>
          <tab name="All" :selected="true">
            <MenuItem :content="menu" />
          </tab>
          <tab name="Breakfast">
            <MenuItem :content="breakfast" />
          </tab>
          <tab name="Lunch">
            <MenuItem :content="lunch" />
          </tab>
          <tab name="Dinner">
            <MenuItem :content="dinner" />
          </tab>
          <tab name="Drink">
            <MenuItem :content="drink" />
          </tab>
        </tabs>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import MenuItem from "@/components/MenuItem.vue";

Vue.use(VueAxios, axios);

Vue.component("tabs", {
  template: `
        <div>
            <div class="tabs">
              <ul>
                <li v-for="tab in tabs" :class="{ 'is-active': tab.isActive }">
                    <a :href="tab.href" @click="selectTab(tab)">{{ tab.name }}</a>
                </li>
              </ul>
            </div>
            <div class="tabs-details">
                <slot></slot>
            </div>
        </div>
    `,
  data() {
    return { tabs: [] };
  },
  created() {
    this.tabs = this.$children;
  },
  methods: {
    selectTab(selectedTab) {
      this.tabs.forEach((tab) => {
        tab.isActive = tab.name == selectedTab.name;
      });
    },
  },
});

Vue.component("tab", {
  template: `
        <div v-show="isActive"><slot></slot></div>
    `,
  props: {
    name: { required: true },
    selected: { default: false },
  },
  data() {
    return {
      isActive: false,
    };
  },
  computed: {
    href() {
      return "#" + this.name.toLowerCase().replace(/ /g, "-");
    },
  },
  mounted() {
    this.isActive = this.selected;
  },
});

export default {
  components: {
    MenuItem,
  },
  data() {
    return {
      menu: [],
      breakfast: [],
      lunch: [],
      dinner: [],
      drink: [],
    };
  },
  mounted() {
    const url = "assets/json/menu.json";
    axios.get(url).then((response) => {
      this.menu = response.data;
      this.pageBreakfast();
      this.pageLunch();
      this.pageDinner();
      this.pageDrink();
    });
  },
  methods: {
    pageBreakfast() {
      for (let i = 0; i < this.menu.length; i++) {
        let item = this.menu[i];
        if (item.group == "Breakfast") {
          this.breakfast.push(item);
        }
      }
    },
    pageLunch() {
      for (let i = 0; i < this.menu.length; i++) {
        let item = this.menu[i];
        if (item.group == "Lunch") {
          this.lunch.push(item);
        }
      }
    },
    pageDinner() {
      for (let i = 0; i < this.menu.length; i++) {
        let item = this.menu[i];
        if (item.group == "Dinner") {
          this.dinner.push(item);
        }
      }
    },
    pageDrink() {
      for (let i = 0; i < this.menu.length; i++) {
        let item = this.menu[i];
        if (item.group == "Drink") {
          this.drink.push(item);
        }
      }
    },
  },
};
</script>