<template>
  <div class="cards workOuterShell">
    <div class="item" v-for="item in content" :key="item.id">
      <div class="menu-card">
        <div class="menu-img">
          <img :src="item.img" alt="" />
          <div class="price">{{ item.price }}</div>
          <div class="old-price">{{ item.oldPrice }}</div>
        </div>
        <div class="description">
          <h3>{{ item.name }}</h3>
          <p>{{ item.about }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "MenuItem",
  props: {
    content: Array,
  },
  mounted() {
    this.pagin();
  },
  methods: {
    pagin() {
      //debugger;
      let work = document.querySelector(".workOuterShell"),
        items = Array.from(work.querySelectorAll("div.item")),
        maxItems = 9,
        //loadItems = 9,
        hiddenClass = "hiddenStyle";
      //hiddenItems = Array.from(document.querySelectorAll(".hiddenStyle"));
      items.forEach(function (item, index) {
        if (index > maxItems - 1) {
          item.classList.add(hiddenClass);
        }
      });
    },
  },
};
</script>